*{
  padding: 0px;
  margin: 0px;
  font-family:'Courier New', Courier, monospace;
  font-size: 20px;
  color: #ff8c9b;
}

body{
  background-color: #067960;
  margin: 8px;
}


h1{
  padding-top: 30px ;
  text-align: center;
  color: rgb(255, 255, 117);
  font-family: 'Courier New', Courier, monospace;
  font-size:80px;
}
.container{
  width: 100%;
  margin: 0;
  padding-top: 20px;
  display: flex;
  
}
.HomeNav, .PokeNav, .AboutNav{
  text-align: center;
  margin: 30px 0 30px 0;
  text-decoration: none;
  font-size: 20px;
  transition: all 0.2s ease-in-out;
}

.AboutNav:hover, .HomeNav:hover, .PokeNav:hover{
  transform: scale(1.02);
}

.left-content{
  display: grid;
  grid-template-columns:repeat(2, 1fr);
  grid-gap:2rem;
}

.SearchBar input{
  width: 400px;
  text-align: center;
  border: black;
  border-radius: 10px;
  padding: 5px;  
  color:#00b3ff
}

.card{
  width: 285px;
  background-color:#9EDEF9;
  border-radius: 0.5rem;
  box-shadow: 0 5px 5px rgba(0,0,0,0.5);
  padding: 5px;
  display:grid;
  align-items: center;
  justify-content: center; 
  transition: 0.15s;
}

.card:hover{
  transform: scale(1.02);
}

.card h1, h2{
  color: ff8c9b;
}
.right-content{
  width: 100%;
  text-align: center;

}
.right-content h1{
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1.5px;
  color: rgb(255, 255, 117);
  font-size: 70px;
}
.right-content img{
  margin: 0.5rem;
  height: 300px;

}

.abilities{
  width: 70%;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
}
.abilities .group{
  background-color: #b74555;
  padding: 0.5rem;
  font-size: 12px;
  border-radius: 8px;
  margin-left: 1rem;
}
.base-stat{
  margin-top: 1rem;
  font-size: 20px;
}
.btn-group{
  display: flex;
}
.btn-group button{
  outline: none;
  border: none;
  width: 150px;
  padding: 0.3rem 0;
  margin: 1rem;
  font-size: 17px;
  font-weight: bold;
  color: white;
  background-color: #b74555;
  border-radius: 8px;
  text-transform: uppercase;
  transition: all 0.2s ease-in-out;
}

.btn-group button:hover{
  color: rgba(255, 255, 255, 1);
  box-shadow: 0 10px 20px rgba(250, 0, 0, 0.4);
}

h2{
  padding: 10px 0 10px 0;
}

.home p{
  text-align: center;
  padding-top: 80px;
  font-size: 30px;
}

